<template>
  <v-container fluid>
    <c-new-task></c-new-task>
    <v-layout row justify-center v-if="isGlobalLoading">
      <v-progress-circular :size="50" indeterminate color="green darken-3"></v-progress-circular>
    </v-layout>
    <div v-else>
      <v-layout v-if="!getTasks.length" row justify-center>
        <v-flex xs12 lg7>
          <p class="headline grey--text text--darken-2">Упс... У вас поки нема завдань. </p>
        </v-flex>
      </v-layout>
      <transition-group v-else name="slide-y-transition">
        <v-layout row justify-center v-for="task in getTasks" :key="task.id">
          <v-flex xs12 lg7 mb-3>
            <v-card class="elevation-5" :class="{border: task.isDone }">
              <v-card-title primary-title>
                <v-layout row align-center>
                  <div>
                    <i class="grey--text body-2 px-2">
                      {{task.createTime | date}}
                      <span v-if="task.deadline">- {{task.deadline | date}}</span>
                    </i>
                    <div class="headline mb-1 px-2" @click="selectedTask(task.id)" style="cursor:pointer">{{task.title}}</div>
                  </div>
                  <v-spacer></v-spacer>
                  <v-btn icon @click="selectedTask(task.id)">
                    <v-icon color="grey">{{ task.isSelected ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
                  </v-btn>
                </v-layout>
              </v-card-title>
              <v-divider></v-divider>
              <transition name="slide-y-transition">
                <v-card-text v-if="task.isSelected && task.description">
                  {{task.description}}
                </v-card-text>
              </transition>
              <transition name="slide-y-transition">
                <v-divider v-if="task.isSelected && task.description"></v-divider>
              </transition>
              <transition name="slide-y-transition">
                <v-card-actions v-if="task.isSelected">
                  <template v-if="task.deadline">
                    <c-countdown :date="task.deadline"></c-countdown>
                  </template>
                  <v-spacer></v-spacer>
                  <v-btn icon flat @click="checkTask(task.id, task.isDone)" :disabled="isLoading">
                    <v-icon color="green darken-3" v-if="task.isDone">check</v-icon>
                    <v-icon color="grey" v-else>radio_button_unchecked</v-icon>
                  </v-btn>
                  <v-btn icon flat color="red" @click="deleteTask(task.id)">
                    <v-icon>delete</v-icon>
                  </v-btn>
                </v-card-actions>
              </transition>
            </v-card>
          </v-flex>
        </v-layout>
      </transition-group>
    </div>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Home",
  components: {
    "c-new-task": () => import(`@/components/NewTask.vue`),
    "c-countdown": () => import(`@/components/Countdown.vue`)
  },
  computed: {
    ...mapGetters(["getTasks", "isLoading", "isGlobalLoading"])
  },
  created() {
    this.$store.dispatch("fetchTasks");
  },
  methods: {
    selectedTask(id) {
      this.$store.dispatch("selectedTask", { id: id });
    },
    deleteTask(id) {
      this.$store.dispatch("deleteTask", { id: id });
    },
    checkTask(id, isDone) {
      this.$store.dispatch("checkTask", { id: id, isDone: isDone });
    }
  }
};
</script>
 
<style>
.heading {
  font-weight: 500;
  font-size: 18px;
}
.border {
  border-left: #4caf50 3px solid;
}
</style>



